.App {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align:left;
}





body {
 
  background: #f0f2f5;
  font-family: Arial, sans-serif;
}
.box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}
.title-box {
  width: 500px;
}
.title-box img {
  width: 235px;
  margin-bottom: -10px;
}
.title-box p {
  color: #000;
  font-size: 26px;
  font-weight: normal;
  line-height: 32px;
}


.form-box {
  width: 350px;
  padding: 15px;
  text-align: center;
  border-radius: 5px;
  background: #fff;
  margin-left: 60px;
  box-shadow: 0px 2px 10px 1px rgba(71, 71, 71, 0.52);
}

.form-box button {
  width: 100%;
  margin-bottom: 15px;
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  border-radius: 5px;
  border: none;
  padding: 13px 0;
  cursor: pointer;
  background: #166fe5;
}
.form-box button:hover {
  background: #1877f2;
}
.form-box a {
  color: #166fe5;
  font-size: 14px;
  text-decoration: none;
  margin-top: 5px;
  margin-bottom: 20px;
  display: block;
}
.form-box a:hover {
  text-decoration: underline;
}
.form-box hr {
  border: 1px solid #dadde1;
  margin-bottom: 15px;
}
.form-box .create-btn a {
  color: #fff;
  font-size: 16px;
  text-decoration: none;
  padding: 15px 20px;
  border-radius: 5px;
  background: #36a420;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 5px;
}
.form-box .create-btn a:hover {
  background: #42b72a;
}


input {
  width: 100%;
  margin-bottom: 15px;
  padding: 15px;
  font-size: 18px;
  box-sizing: border-box;
  border: 1px solid #eeebeb;
  border-radius: 5px;
  outline: none;
}
input:focus {
  box-shadow: 0px 0px 1px 1px rgb(22, 111, 229);
}

.form-box a {
  color: #166fe5;
  font-size: 14px;
  text-decoration: none;
  margin-top: 5px;
  margin-bottom: 20px;
  display: block;
}
.form-box a:hover {
  text-decoration: underline;
}

